import React from 'react'
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import Modal from 'react-bootstrap/Modal';
import Lottie from 'react-lottie';
import animationData from 'assets/lottie/Rewards.json'
import AdminServices from 'services/api/AdminServices';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import { toast } from 'react-toastify';
import db from 'libs/db';

const RewardsScreen = (props) => {

	const [showTermsModal, setShowTermsModal] = React.useState(false);
	const [showKnowMoreModal, setShowKnowMoreModal] = React.useState(false);
	const [rewardsStatus, setRewardsStatus] = React.useState({});
	const [loading, setLoading] = React.useState(true);

	const defaultOptions = {
		loop: true,
		autoplay: true,
		animationData: animationData,
		rendererSettings: {
			preserveAspectRatio: 'xMidYMid slice'
		}
	};

	React.useEffect(() => {
		try {
			db.get(db.model.user).then(async (user) => {
				var a = await AdminServices.getRewardsStatus(user.userData.id).then((res) => res)
				console.log(a)
				setRewardsStatus(a.data)
				setLoading(false)
			})
		} catch (error) {
			toast.error(error);
			setLoading(false)
		}
	}, [])

	const TermsModal = () => {
		return (
			<Modal
				className="bottom-drawer"
				show={showTermsModal}
				onHide={() => setShowTermsModal(false)}
				keyboard={false}
			>
				<Modal.Body>
					<div className='header2dark'>Terms and conditions</div>
					<ul className='smallTextDark' style={{ marginTop: '4%', marginBottom: 0 }}>
						<li>25 winners will be chosen at random from the first 100 investors of mintbox for rewards</li>
						<li>We will release the list of 25 winners after at least 100 investors have invested in mintbox</li>
						<li>The winners will receive Amazon gift cards on their email IDs used for signing up on mintbox</li>
						<li>To qualify, you must be invested in at least one mintbox during the time of winner choosing</li>
					</ul>
				</Modal.Body>
				<Modal.Footer>
					<button className="solidButton" onClick={() => setShowTermsModal(false)}>
						Got it!
					</button>
				</Modal.Footer>
			</Modal>
		)
	}

	const KnowMoreModal = () => {
		return (
			<Modal
				className="bottom-drawer"
				show={showKnowMoreModal}
				onHide={() => setShowKnowMoreModal(false)}
				keyboard={false}
			>
				<Modal.Body>
					<div className='header2dark'>How did you make it?</div>
					<div className='smallTextDark' style={{ marginTop: '4%' }}>mintbox rewards are reserved for the first 100 investors of mintbox. You have qualified because you are among the first 100. We will reach out to you if you win the reward.</div>
					<div className='header2dark' style={{ marginTop: '4%' }}>What is the reward?</div>
					<div className='smallTextDark' style={{ marginTop: '4%' }}>A Rs. 500 Amazon gift card!</div>
					<div className='secondaryGreen' style={{ marginTop: '4%' }} onClick={()=>{setShowKnowMoreModal(false);setShowTermsModal(true)}}>Terms and conditions</div>
				</Modal.Body>
				<Modal.Footer>
					<button className="solidButton" onClick={() => setShowKnowMoreModal(false)}>
						Got it!
					</button>
				</Modal.Footer>
			</Modal>
		)
	}

	return (
		<div className='mainContainer'>
			<MainHeader
				header="Invest to win"
				navigation={props.history}
				right='Terms and conditions'
				rightAction={() => setShowTermsModal(true)}
			/>
			{
				loading ?
					<ActivityLoaderMintbox />
					:
					<div className="dataContainer" style={{ textAlign: "center" }}>
						<Lottie options={defaultOptions} style={{ marginTop: '20%' }} />
						<div className='bigHeader' style={{ margin: "4% 10%" }}>
							{
								rewardsStatus.IsEligible ?
									'Congratulations! You have qualified for a reward'
									:
									'Start investing in a mintbox and win Rs. 500 as Amazon gift card!'
							}
						</div>
						{
							rewardsStatus.IsEligible ?
								<div className='header2dark secondaryGreen' style={{ marginTop: '6%' }} onClick={() => setShowKnowMoreModal(true)}>
									Know more
								</div>
								:
								<>
									<button className='solidButton' style={{ width: "auto", margin: "6% auto", paddingLeft: "20px", paddingRight: "20px" }}
										onClick={() => props.history.push("/Invest")}
									>
										Explore mintboxes
									</button>
									<div className='header2dark secondaryGreen' onClick={() => props.history.push("/risk-profile")}>Find a mintbox for me!</div>
								</>
						}
					</div>
			}
			<KnowMoreModal />
			<TermsModal />
		</div>
	)
}

export default RewardsScreen
